.App {
  text-align: center;
}

body,html, .App{
    height: 100%;
}

.calc{
    width:50%;
}
.App {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  flex-wrap: wrap;
   
    
}
#root{
    width: 100%;
}

.App-link {
  color: #61dafb;
}

.button{
  
        width: 18%;
    display: inline-block;
    background-color: white;
    margin: 1%;
    padding: 1vw;
    color: black;
}
h3{
    text-align: right;
    
}

#equal{
    flex-grow: 1;
}
.button:hover{
    background-color: cadetblue;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
